body {
  background-color: black;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;  
  background-image: url("../assets/images/bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  color: #a7906f;
}

main {
  display: flex;
  width: 100vw;
  height: 100vh;
  align-items: center;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 1000px;
  height: 800px;
  border-radius: 10px;
  background-color: #1d2025;
  background: linear-gradient(0deg, #1d2025, #42464d);
  margin: auto;
}

.title {
  margin-top: 30px;
  margin-bottom: 0px;
  font-size: 40px;
  font-weight: 300;
}

.subtitle {
  margin: 0px 0px 50px 0px;
  font-weight: 100;
}

.main-container {
  display: flex;
  justify-content: space-around;

}

.unlock-list {
  width: 310px;
  background-color: #181a1f;
  margin: 5px;
  padding: 20px;
  border-radius: 5px;;
}

.total-points {
  margin: 5px;
  font-weight: 600;
}

.challenge-item {
  width: 300px; 
  background-color: transparent;
  color: #a7906f;
  display: flex;
  justify-content: space-between;
  padding: 5px;
  border: 1px solid grey; 
  border-radius: 2px;
  margin: 5px;
}

.challenge-item-check {
  background-color: rgb(0, 75, 0);
}

.slot-area {
  display: grid;
  grid-template-columns: 100px 50px 100px 50px 100px;
  grid-gap: 10px;
  justify-content: center;
  padding: 20px;
  margin: 5px;
  background-color: #181a1f;
  border-radius: 5px;;
}

.slot-card {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  background-color: white;
  width: 100px;
  height: 150px;
  box-shadow: 0px 2px 2px rgba(0,0,0,0.15);
  border-radius: 5px;
  border: none;
}

.slot-card-mainhand {
  grid-row: 2 
}

.slot-card-head {
  grid-row: 1;
  grid-column: 3;
}

.slot-card-body {
  grid-row: 2;
  grid-column: 3;
}

.slot-card-offhand {
  grid-row: 2;
  grid-column: 5;
}

.slot-card-gloves {
  grid-row: 3;
  grid-column: 1; 
  height: 100px;
}

.slot-card-boots {
  grid-row: 3;
  grid-column: 5; 
  height: 100px;
}

.slot-card-belt {
  grid-row: 3;
  grid-column: 3; 
  height: 50px;
}

.slot-card-plug {
  grid-row: 4;
  grid-column: 3; 
  height: 100px;
  background-image: url("../assets/images/plug.png");
  background-size: contain;
}

.slot-card-ringleft {
  align-self: flex-end;
  grid-row: 2;
  grid-column: 2; 
  height: 50px;
  width: 50px;
}

.slot-card-ringright {
  align-self: flex-end;
  grid-row: 2;
  grid-column: 4; 
  height: 50px;
  width: 50px;
}

.slot-card-amulet {
  align-self: flex-end;
  grid-row: 1;
  grid-column: 4; 
  height: 50px;
  width: 50px;
}

.slot-title {
  margin: 0px;
  font-weight: 100;
}

.slot-card-locked {
  background-color: rgb(104, 3, 3);
}

.slot-card-normal {
  background-color: white;
}

.slot-card-magic {
  background-color: #9494fb;
}

.slot-card-rare {
  background-color: #f6f571;
}

.slot-card-unique {
  background-color: #9c541a;
}

.slot-level {
  display: flex;
  top: 5px;
  right: 5px;
  justify-content: center;
  align-items: center;
  position:absolute;
  font-size: 8px;
  min-width: 20px;
  min-height: 20px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.496);
  color: white;
  
}